import * as React from "react";
import {Link} from "gatsby";
import Footer from "./footer";

const layoutStyle = {
    margin: 0,
    padding: 0,
    backgroundColor: "#181a1b",
    color: "#9e6ecf",
    fontFamily: "sans-serif",
    height: "100vh",
}
const titleStyle = {
    paddingTop: "50px",
    paddingBottom: "25px",
}
const linksStyle = {
    listStyle: "none",
    display: "flex",
    paddingLeft: 0,
    paddingBottom: "25px",
}
const linkStyle = {
    paddingRight: "2rem",
}
const container = {
    width: "600px",
    margin: "auto",
}

const Layout = ({title, headline, children}) => {
    return(
        <div style={layoutStyle}>
            <title>{title} | Martin Berg Alstad</title>
            <div style={container}>
                <h1 style={titleStyle}>{headline}</h1>
                <ul style={linksStyle}>
                    <li style={linkStyle}><Link to={"/"}>Home</Link></li>
                    <li style={linkStyle}><Link to={"/projects"}>Projects</Link></li>
                    <li style={linkStyle}><Link to={"/contact-me"}>Contact me</Link></li>
                </ul>
                <main>
                    {children}
                    <Footer/>
                </main>
            </div>
        </div>
    )
}

export default Layout
